.top-line__box {
    background-color: #343434;
    padding: 16px 0;
    color: #fff;
}
.top-line__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.top-line__phone {
    margin-right: 23px;
}
.top-line__register {
    &:not(:last-child) {
        margin-right: 3px;
    }
}
.top-line__text {
    font-weight: 400;
    font-size: 12px;
    line-height: calc(16 / 12);
}